.text-field {
  display: block;
}
.textfield--full-width {
  width: 100%;
}
.textfield--padded {
  padding: 5px 10px;
}

.textfield--ellipsis {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}
.textfield--italic {
  font-style: italic;
}
.textfield--lables {
  font-weight: bold;
}
.textfield-pagination {
  font-size: 1.1em;
  margin-right: 10px;
}



